.intro_img {
    width: 1400px;
    height: 290px;
    border-radius: 14px!important;
}
.Profile_page {
    margin-left: 250px;
    width: 1400px;
    .subscribe {
        border: none;
        margin-top: 10px;
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #FFFFFF;
        padding: 10px 20.5px;
        background: #FF0000;
        border-radius: 20px;
    }
    .profile_qismi {
        display: flex;
        margin-top: 40px;
        justify-content: space-between;
        img {
            border-radius: 50%;
            width: 80px;
            height: 80px;
        }
        .main-profil {
            display: flex;
            div {
                margin-top: 10px;
                margin-left: 10px;
            }
            h1 {
                font-style: normal;
                font-weight: bold;
                font-size: 26px;
                line-height: 33px;
                color: #FFFFFF;
            }
            p {
                font-style: normal;
                font-weight: normal;
                font-size: 13px;
                line-height: 16px;

                color: #C2C2C2;
            }
        }
    }
}
.profile-wrapper {
    ul {
        display: flex;
        .profile-link-home {
            color: #FF0000;
            padding-bottom: 5px;
            border-bottom: 3px solid #FF0000;
        }
        li {
            margin-right: 69px;
            margin-top: 50px;
            margin-left: 20px;
            
            a {
                font-style: normal;
                font-weight: normal;
                font-size: 18px;
                line-height: 23px;
                /* identical to box height */
                color: #FFFFFF;
            }
        }
    }
}
.button-12 {
    position: absolute;
    width: 520px;
    height: 250px;
    background-color: inherit;
    z-index: 5000;
    border: 2px solid inherit;
    margin-left: -520px;
    border-radius: 14px;
}
.glv-video {
    margin-top: 59px;
    display: flex;
    div {
        margin-left: 20px;
        h3 {
            width: 430px;
            margin-top: 20px;
            font-family: Mulish;
            font-style: normal;
            font-weight: bold;
            font-size: 26px;
            line-height: 34px;
            color: #FFFFFF;
        }
        p {
            width: 540px;
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 22px;
            color: #FFFFFF;
            mix-blend-mode: normal;
            opacity: 0.6;
            padding: 15px 0;
        }
    }
}
.glv-video-video {
    border-radius: 14px;
    position: relative;
}
.glv-wrapper {
    display: flex;
    .profile-user {
        margin-left: 100px;
        margin-top: -25px;
        
        h2 {
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #C2C2C2;
            margin-bottom: 20px;
        }
        .profile-link-2  {
            display: flex;
            padding: 14px 0;
            h1 {
                font-style: normal;
                font-weight: bold;
                font-size: 18px;
                line-height: 23px;
                color: #FFFFFF;
                margin-top: 10px;
                margin-left: 10px;
            }
            img {
                width: 50px;
                height: 50px;
                border-radius: 50%;
            }
        }
    }
}
.next-video {
    margin-top: 20px;
    h1 {
        font-style: normal;
        font-weight: bold;
        font-size: 26px;
        line-height: 33px;
        color: #FFFFFF;
    }
}
.button11 {
    position: absolute;
    width: 260px;
    height: 170px;
    margin-top: -170px;
    background-color: inherit;
    border: none;
    z-index: 5;
}