header {
    width: 100%;
    background-color: #18151E;
    position: fixed;
    top: 0;
    nav {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        img {
            margin: 10px;
        }
        .nav-link {
            display: flex;
            color: #fff;
            font-weight: bold;
            margin-top: 7px;
            img {
                margin: 0px;
                margin-left: 20px;
            }
            h1 {
                font-weight: bolder;
                font-size: 30px;
            }
        }
        input {
            background: #EBEBEB!important;
            mix-blend-mode: normal;
            opacity: 0.1;
            border-radius: 22px;
            margin-left: 60px;
            width: 830px;
            padding: 15px!important;
        }
        .serch_img {
                width: 19px;
                height: 19px;
                margin-left: -40px;
                margin-top: 15px;
        }
        div {
            display: flex;
        }
        .nav-left {
            .nav-img {
                margin-top: 5px;
            }
            .last-nav-img {
                margin-top: -5px;
            }
            img {
                margin: 3px 25px;
                align-items: center;
            }
            img:hover {
                cursor: pointer;
            }
        }
    }
}
