.offcanvas-wrapper {
    position: fixed;
    top: 0;
}
.header__link-active {
   h1 {
    color: #FF0000!important;
   }
}
.offcanvas-list {
    margin-top: 80px;
    .Libry {
        margin-top: 20px;
    }
    .offcanvas-link {
        display: flex;
        padding: 12px 10px;
        margin-left: 20px;
        h1 {
            font-family: "Mulish";
            font-style: normal;
            font-weight: normal;
            font-size: 16px;
            line-height: 20px;
            color: #FFFFFF;
            mix-blend-mode: normal;
            opacity: 0.5;
            margin-left: 20px;
        }
    }
}
.subscriptions {
    h1 {
        font-style: normal;
        font-weight: bold;
        font-size: 20px;
        margin-left: 30px;
        margin-top: 30px;
        line-height: 23px;
        color: #FFFFFF;
    }
}
.profile-link {
    display: flex;
    margin-left: 30px;
    margin-top: 20px;
    img {
        width: 26px;
        height: 26px;
        border-radius: 50%;
        margin-right: 7px;
    }
    p {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        line-height: 20px;
        color: #FFFFFF;
        mix-blend-mode: normal;
        margin-top: 5px;
        opacity: 0.5;
    }
}
