.main_viddeo {
    border-bottom: 1px solid #fff;
    width: 1310px;
    iframe {
        width: 1300px;
        height: 700px;
        border-radius: 14px;
    }
    .control {
        .more_img {
            margin-top: 24px;
            margin-left: 30px;
        }
        justify-content: end;
        display: flex;
        margin-top: -70px;
        div {
            width: 120px;
            height: 40px;
            display: flex;
            background: #00000060;
            margin: 10px 5px;
            mix-blend-mode: normal;
            border-radius: 20px;
            img {
                margin-right: 6px;
                margin-top: 12px;
                width: 14px;
                margin-left: 14px;
                height: 12px;
            }
            p {
                margin-top: 10px;
                font-style: normal;
                font-weight: normal;
                font-size: 16px;
                line-height: 20px;
                /* identical to box height */


                color: #FFFFFF;
            }
        }
    }
    
    div {
         h3 {
            font-style: normal;
            font-weight: bold;
            font-size: 36px;
            line-height: 45px;
            color: #FFFFFF;
            margin-top: 10px;
        }
        p {
            font-weight: normal;
            font-size: 20px;
            line-height: 25px;
            padding-bottom: 20px;
            color: #C2C2C2;
        }
    }
}
.videos-wrapper {
    margin-left: 80px;
}
.videos-wrapper {
    display: flex!important;
}
.video-profile-info {
    display: flex;
    margin-top: 40px;
    width: 1300px;
    button {
        justify-content: end;
        background: #FF0000;
        border-radius: 20px;
        font-style: normal;
        padding: 10px 20px;
        font-weight: normal;
        border: none;
        font-size: 16px;
        line-height: 20px;
        margin-left: 220px;
        text-align: center;
        width: 170px;

        color: #FFFFFF;
    }
    .video-profile-content {
        display: flex;
        div {
            margin-left: 10px;
        }
        h1 {
            font-style: normal;
            font-weight: bold;
            font-size: 26px;
            line-height: 33px;
            color: #FFFFFF;
        }
        p {
            font-style: normal;
            font-weight: normal;
            font-size: 13px;
            line-height: 16px;
            width: 820px;
            color: #FFFFFF;
            margin-bottom: 20px;
            mix-blend-mode: normal;
            opacity: 0.4;
        }
        h3 {
            font-style: normal;
            font-weight: normal;
            font-size: 20px;
            line-height: 25px;
            /* identical to box height */


            color: #FFFFFF;

            mix-blend-mode: normal;
            opacity: 0.4;
        }
        img {

            width: 80px;
            height: 80px;
            border-radius: 50%;
        }
    }
}

.not-mein {
    color: #fff ;
    margin-left: 10px;
    .not-mein-intro {
        margin-top: 80px;
        display: flex;
        input {
            margin-top: 20px;
            margin-left: 10px;
        }
        p {
            font-style: normal;
            font-weight: bold;
            font-size: 16px;
            line-height: 20px;
            letter-spacing: 1px;
            text-transform: uppercase;
            color: #FFFFFF;
            margin-left: 120px;
            margin-top: 15px;
        }
        h2 {
            font-style: normal;
            font-weight: bold;
            font-size: 36px;
            line-height: 45px;

            color: #FFFFFF;
        }
    }
}
.next-videos {
    margin: 10px 0;
    div {
        iframe {
            width: 367px;
            height: 213px;
            border-radius: 14px;
        }
        padding: 10px 0;
        
    }
    p {
        color: #fff;
        overflow-y: hidden;
        width: 360px;
        height: 15px;
        overflow-x: hidden;
    }
}
.next-video-link {
    position: absolute;
    width: 367px;
    height: 253px;
    background-color: inherit;
    margin-top: -250px;
}